.swiper-company {
  width: 100%;

  &__slide {
    position: relative;
    display: block;
  }

  &__slide-image {
    height: 100%;
  }

  &__slide-img {
    width: 100%;
    height: 100%;
    min-height: 225px;
  }

  &__slide-content {
    margin-top: 40px;
    padding: 0 10px;
    line-height: 1.5;
    font-size: 14px;
  }

  &__slide-desc {
    max-width: 270px;
  }

  .swiper-pagination-fraction {
    top: 240px;
    bottom: auto;
    padding-left: 60px;
    text-align: left;
    color: black;
    font-size: 14px;
  }

  .swiper-button-next {
    top: 255px;
    left: 110px;
    bottom: auto;
    width: 14px;
    height: 28px;
    background-size: contain;
  }

  .swiper-button-prev {
    top: 255px;
    left: 20px;
    bottom: auto;
    width: 14px;
    height: 28px;
    background-size: contain;
  }

  @media (min-width: @media-sm) {
    margin: 0 auto 100px auto !important;

    &__slide-content {
      position: absolute;
      top: 0;
      left: 35%;
      right: 15%;
      bottom: 0;
      margin-top: 0;
      padding: 40px 30px 0 30px;
      background-color: rgba(0, 0, 0, 0.6);
      font-size: 24px;
      color: white;
    }

    .swiper-pagination-fraction {
      top: auto;
      left: 35%;
      bottom: 65px;
      width: auto;
      color: white;
    }

    .swiper-button-prev {
      left: ~"calc(35% + 30px)";
      bottom: 60px;
      top: auto;
      background-image: url('data:image/svg+xml;base64,');
    }

    .swiper-button-next {
      left: ~"calc(35% + 100px)";
      bottom: 60px;
      top: auto;
      background-image: url('data:image/svg+xml;base64,');
    }
  }

  @media (min-width: @media-md) {
    height: 630px;

    &__slide-content {
      padding: 150px 70px 0 70px;
    }

    &__slide-img {
      width: auto;
    }

    .swiper-pagination-fraction {
      left: ~"calc(35% + 110px)";
      padding-left: 0;
    }

    .swiper-button-prev {
      left: ~"calc(35% + 70px)";
    }

    .swiper-button-next {
      left: ~"calc(35% + 165px)";
    }
  }

  @media (min-width: @media-lg) {

    &__slide-img {
      width: 100%;
    }

    &__slide-content {
      bottom: 0;
    }
  }
}
