.breadcrumbs {

  &__link {
    position: relative;
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    font-family: @FHelvetica-Neue-Roman;
    color: @breadcrumbs;
    font-size: 10px;
    cursor: pointer;

    &:after {
      content: '';
      display: inline-block;
      width: 10px;
      height: 1px;
      margin: 0 0 2px 4px;
      background-color: @breadcrumbs;
      pointer-events: none;
      user-select: none;
    }

    &:last-child {
      pointer-events: none;
      user-select: none;
    }

    &:last-child:after {
      display: none;
    }
  }
}