.table-special {
  display: flex;
  align-items: stretch;
  justify-content: stretch;

  &__item {
    position: relative;
    overflow-x: auto;
    overflow-y: hidden;

    &:before {
      content: '';
      position: absolute;
      top: 1px;
      left: 0;
      bottom: 1px;
      width: 1px;
      background-color: @section-gray;
    }
  }

  @media (min-width: @media-sm) {
    margin: 30px 0;

    .page-table {
      width: 25%;
      margin: 0;
    }

    &__item {
      .page-table {
        width: 100%;
      }
    }
  }

  @media (min-width: @media-md) {
    margin: 60px 0 30px 0;
  }
}