.page-table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  border: 1px solid @table-border;

  &__thead {}

  &__tr {}

  &__th {}

  &__th,
  &__td {
    padding: 10px;
    text-align: center;
    line-height: 1.5;
    border: 1px solid @table-border;
  }

  &__tbody {}

  &__td {
    div {
      white-space: nowrap;
    }
  }

  @media (min-width: @media-sm) {
    margin: 60px 0 30px 0;

    &__th,
    &__td {
      padding: 15px 35px;
    }
  }
}