@font-face {
  font-family: 'Helvetica-Neue-Bold';
  src:
  url('data:application/octet-stream;base64,') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Helvetica-Neue-Roman';
  src:
  url('data:application/octet-stream;base64,') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@FHelvetica-Neue-Bold:                          'Helvetica-Neue-Bold';
@FHelvetica-Neue-Roman:                         'Helvetica-Neue-Roman';

@base-font:                                     @FHelvetica-Neue-Roman, sans-serif;