.page-list {
  list-style: none;
  display: block;
  margin-top: 30px;
  max-width: 600px;
  padding-left: 0;
  font-family: @FHelvetica-Neue-Roman;
  font-size: 12px;
  color: black;

  &_small {
    max-width: 440px;
  }

  &__item {
    position: relative;
    display: block;
    padding-left: 13px;
    padding-bottom: 8px;
    line-height: 1.5;

    &:before {
      content: '';
      position: absolute;
      top: 8px;
      left: 0;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: @blue;
    }
  }

  @media (min-width: @media-sm) {
    font-size: 14px;
  }
}