.video-box {
  display: flex;
  flex-direction: column-reverse;

  &__desc {}

  &__video {

    video {
      max-width: 500px;
    }
  }

  @media (min-width: @media-sm) {
    flex-direction: row;
    align-items: center;

    &__desc {
      max-width: 420px;
      padding-left: 30px;
    }
  }

  @media (min-width: @media-md) {

    &__desc {
      padding-left: 100px;
    }
  }
}