/* - Точки пагинации - */
.point {
  margin-bottom: 25px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
}

.point span {
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.point-active {
  position: relative;
  border: 1px solid #0052ba;
}

.point-active span {
  position: absolute;
  background: #0052ba;
  top: 4px;
  left: 4px;
}

.point-static {
  position: relative;
  border: 1px solid #9b9b9b;
}

.point-static span {
  position: absolute;
  background: #9b9b9b;
  top: 4px;
  left: 4px;
}

