.page-section {
  max-width: 1920px;
  padding: 30px 20px;
}
.page-section_gray {
  background-color: #f5f5f6;
}
@media (min-width: 768px) {
  .page-section {
    padding: 50px;
  }
}
@media (min-width: 1024px) {
  .page-section {
    padding: 60px 200px;
  }
}
.page-title {
  line-height: 36px;
  font-family: 'Helvetica-Neue-Bold';
  font-size: 24px;
  color: black;
}
.page-title_thin {
  font-family: 'Helvetica-Neue-Roman';
}
@media (min-width: 768px) {
  .page-title {
    padding: 45px 0 40px 0;
    font-size: 50px;
  }
}
.page-desc {
  max-width: 600px;
  line-height: 1.5;
  font-family: 'Helvetica-Neue-Roman';
  font-size: 14px;
  color: black;
}
.page-desc-title {
  font-family: 'Helvetica-Neue-Roman';
  font-size: 24px;
  color: black;
}
.page-list {
  list-style: none;
  display: block;
  margin-top: 30px;
  max-width: 600px;
  padding-left: 0;
  font-family: 'Helvetica-Neue-Roman';
  font-size: 12px;
  color: black;
}
.page-list_small {
  max-width: 440px;
}
.page-list__item {
  position: relative;
  display: block;
  padding-left: 13px;
  padding-bottom: 8px;
  line-height: 1.5;
}
.page-list__item:before {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #0052ba;
}
@media (min-width: 768px) {
  .page-list {
    font-size: 14px;
  }
}
.page-subtitle {
  margin: 0 0 13px 0;
  line-height: 1.2;
  font-family: 'Helvetica-Neue-Roman';
  font-size: 24px;
  color: black;
}
@media (min-width: 768px) {
  .page-subtitle {
    font-size: 42px;
  }
}
.page-btn {
  margin: 50px 0;
}
.page-table {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
  border: 1px solid #d6dbdf;
}
.page-table__th,
.page-table__td {
  padding: 10px;
  text-align: center;
  line-height: 1.5;
  border: 1px solid #d6dbdf;
}
.page-table__td div {
  white-space: nowrap;
}
@media (min-width: 768px) {
  .page-table {
    margin: 60px 0 30px 0;
  }
  .page-table__th,
  .page-table__td {
    padding: 15px 35px;
  }
}
.page-img {
  width: 100%;
  max-width: 100%;
}
@media (min-width: 768px) {
  .page-img {
    width: auto;
  }
}
.breadcrumbs__link {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  font-family: 'Helvetica-Neue-Roman';
  color: #5c708b;
  font-size: 10px;
  cursor: pointer;
}
.breadcrumbs__link:after {
  content: '';
  display: inline-block;
  width: 10px;
  height: 1px;
  margin: 0 0 2px 4px;
  background-color: #5c708b;
  pointer-events: none;
  user-select: none;
}
.breadcrumbs__link:last-child {
  pointer-events: none;
  user-select: none;
}
.breadcrumbs__link:last-child:after {
  display: none;
}
.video-box {
  display: flex;
  flex-direction: column-reverse;
}
.video-box__video video {
  max-width: 500px;
}
@media (min-width: 768px) {
  .video-box {
    flex-direction: row;
    align-items: center;
  }
  .video-box__desc {
    max-width: 420px;
    padding-left: 30px;
  }
}
@media (min-width: 1024px) {
  .video-box__desc {
    padding-left: 100px;
  }
}
.swiper-company {
  width: 100%;
}
.swiper-company__slide {
  position: relative;
  display: block;
}
.swiper-company__slide-image {
  height: 100%;
}
.swiper-company__slide-img {
  width: 100%;
  height: 100%;
  min-height: 225px;
}
.swiper-company__slide-content {
  margin-top: 40px;
  padding: 0 10px;
  line-height: 1.5;
  font-size: 14px;
}
.swiper-company__slide-desc {
  max-width: 270px;
}
.swiper-company .swiper-pagination-fraction {
  top: 240px;
  bottom: auto;
  padding-left: 60px;
  text-align: left;
  color: black;
  font-size: 14px;
}
.swiper-company .swiper-button-next {
  top: 255px;
  left: 110px;
  bottom: auto;
  width: 14px;
  height: 28px;
  background-size: contain;
}
.swiper-company .swiper-button-prev {
  top: 255px;
  left: 20px;
  bottom: auto;
  width: 14px;
  height: 28px;
  background-size: contain;
}
@media (min-width: 768px) {
  .swiper-company {
    margin: 0 auto 100px auto !important;
  }
  .swiper-company__slide-content {
    position: absolute;
    top: 0;
    left: 35%;
    right: 15%;
    bottom: 0;
    margin-top: 0;
    padding: 40px 30px 0 30px;
    background-color: rgba(0, 0, 0, 0.6);
    font-size: 24px;
    color: white;
  }
  .swiper-company .swiper-pagination-fraction {
    top: auto;
    left: 35%;
    bottom: 65px;
    width: auto;
    color: white;
  }
  .swiper-company .swiper-button-prev {
    left: calc(35% + 30px);
    bottom: 60px;
    top: auto;
    background-image: url('data:image/svg+xml;base64,');
  }
  .swiper-company .swiper-button-next {
    left: calc(35% + 100px);
    bottom: 60px;
    top: auto;
    background-image: url('data:image/svg+xml;base64,');
  }
}
@media (min-width: 1024px) {
  .swiper-company {
    height: 630px;
  }
  .swiper-company__slide-content {
    padding: 150px 70px 0 70px;
  }
  .swiper-company__slide-img {
    width: auto;
  }
  .swiper-company .swiper-pagination-fraction {
    left: calc(35% + 110px);
    padding-left: 0;
  }
  .swiper-company .swiper-button-prev {
    left: calc(35% + 70px);
  }
  .swiper-company .swiper-button-next {
    left: calc(35% + 165px);
  }
}
@media (min-width: 1366px) {
  .swiper-company__slide-img {
    width: 100%;
  }
  .swiper-company__slide-content {
    bottom: 0;
  }
}
