#nav_open span:nth-child(2) {
  top: 8px;
}
#nav_open span:nth-child(3) {
  top: 16px;
}
#nav_open.open span {
  background: black;
}
#nav_open.open span:nth-child(1) {
  top: 9px;
  transform: rotate(135deg);
}
#nav_open.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}
#nav_open.open span:nth-child(3) {
  top: 9px;
  transform: rotate(-135deg);
}
